.form {
  display: flex;
}

.form input {
  flex: 1; /* 버튼을 뺀 빈 공간을 모두 채워줍니다 */
  font-size: 1.25rem;
  outline: none;
  border: none;
  background-color: #FFFDEA;
  color: #FFA500;
  border-radius: 20px;
  padding: 0 1.5rem;
}

.create-button {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  margin-left: 1rem;
  background: #FFA500;
  border-radius: 20px;
  color: white;
  font-weight: 500;
  cursor: pointer;
}

.create-button:hover {
  background: #FFBF66;
}
